import React, { useState, useEffect } from 'react'
import Card from 'react-bootstrap/Card'
import Button from 'react-bootstrap/Button';
import { useHistory } from "react-router-dom";
import Axios from 'axios';
import bg from './assets/bg.jpg';
import {
  Form,
  FormGroup,
  Input,
  Label,
} from 'reactstrap';

import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import { config } from './config';
import NavBar from './components/NavBar/NavBar';
import Select from 'react-select'

const Settings = () => {
  const tokenString = sessionStorage.getItem('token');

  const [backend_error, setbackendError] = useState('');
  const [errors, setErrors] = useState('');
  const [isSubmitted, setIsSubmitted] = useState(false);
  const history = useHistory();
  const Districts = [
    { value: "ADALAR", label: "ADALAR" },
    { value: "ARNAVUTKY", label: "ARNAVUTKY" },
    { value: "ATAEHR", label: "ATAEHR" },
    { value: "AVCILAR", label: "AVCILAR" },
    { value: "BACILAR", label: "BACILAR" },
    { value: "BAHELEVLER", label: "BAHELEVLER" },
    { value: "BAKIRKY", label: "BAKIRKY" },
    { value: "BAAKEHR", label: "BAAKEHR" },
    { value: "BAYRAMPAA", label: "BAYRAMPAA" },
    { value: "BEYKOZ", label: "BEYKOZ" },
    { value: "BEYLKDZ", label: "BEYLKDZ" },
    { value: "BEYOLU", label: "BEYOLU" },
    { value: "BYKEKMECE", label: "BYKEKMECE" },
    { value: "ATALCA", label: "ATALCA" },
    { value: "EKMEKY", label: "EKMEKY" },
    { value: "ESENLER", label: "ESENLER" },
    { value: "ESENYURT", label: "ESENYURT" },
    { value: "EYPSULTAN", label: "EYPSULTAN" },
    { value: "FATH", label: "FATH" },
    { value: "GAZOSMANPAA", label: "GAZOSMANPAA" },
    { value: "GNGREN", label: "GNGREN" },
    { value: "KADIKY", label: "KADIKY" },
    { value: "KAITHANE", label: "KAITHANE" },
    { value: "KARTAL", label: "KARTAL" },
    { value: "KKEKMECE", label: "KKEKMECE" },
    { value: "MALTEPE", label: "MALTEPE" },
    { value: "KKEKMECE", label: "KKEKMECE" },
    { value: "PENDK", label: "PENDK" },
    { value: "SANCAKTEPE", label: "SANCAKTEPE" },
    { value: "SARIYER", label: "SARIYER" },
    { value: "SLVR", label: "SLVR" },
    { value: "SULTANBEYL", label: "SULTANBEYL" },
    { value: "SULTANGAZ", label: "SULTANGAZ" },
    { value: "LE", label: "LE" },
    { value: "L", label: "L" },
    { value: "TUZLA", label: "TUZLA" },
    { value: "MRANYE", label: "MRANYE" },
    { value: "SKDAR", label: "SKDAR" },
    { value: "ZEYTNBURNU", label: "ZEYTNBURNU" }
  ]

  const handleChangeUserInfo = e => {
    console.log(e);
    const {name, value} = e;
    setValues({
      ...values,
      [name] : value
    });
    setValues({
      ...values,
      ["old_name"] : e.name,
      ["old_email"] : e.email,
      ["old_address"] : e.address,
      ["old_phone"] : e.phone,
    });
    values.name = e.name;
    values.email = e.email;
    values.phone = e.phone;

    values.old_name = e.name;
    values.old_email = e.email;
    values.old_address = e.address;
    values.old_phone = e.phone;
  };

  const handleChange = e => {
    console.log(e);
    const {name, value} = e.target;
    setValues({
      ...values,
      [name] : value
    });
  };

  const handleChangeAddress = e => {
    const p_value = e.value;
    setValues({
      ...values,
      ["address"] : p_value
    });
    values.address = e.value;
  };

  const handleSubmit = e => {
    e.preventDefault();
    updateUser();
  }

  const updateUser = () => {
    Axios.post(`${config.SERVER_URI}/api/update-user`,
    {
      name: values.name || values.old_name,
      email: values.email || values.old_email,
      address: values.address.toLowerCase() || values.old_address.toLowerCase(),
      phone: values.phone || values.old_phone,
      user_id: tokenString
    }).then(response => {
      setIsSubmitted(true);
      history.push("/feed");
    }).catch(error => {
        let err = error.response.data.errors[0].msg;
        if (err){
          console.log(err);
          setbackendError(err);
          history.push("/settings");
          err ='';
        }
        else{
          setbackendError('');
          history.push("/feed");
          setIsSubmitted(true);
      }});
  }
  
  const [values, setValues] = useState({
    name: '',
    email: '',
    address: '',
    phone: '',
    old_name: '',
    old_email: '',
    old_address: '',
    old_phone: ''
  });

  function logout(e) {
    console.log("out");
    Axios.post(`${config.SERVER_URI}/api/logout`,
    {
      logout: true,
    }).then((response) => {
      if (!response){
        console.log("no error");
      }
      else{
        console.log(response);
        sessionStorage.removeItem('token');
      }
        }).catch(error => {
        console.log(error);
        let err = error.response;
        if (err){
          console.log(err);
        }});
      history.push("/");
      history.go(0);
    };

  const getInfo = () => {
    Axios.post(`${config.SERVER_URI}/api/get-user-info`,
    {
      user_id: tokenString
    }).then(res => {
      handleChangeUserInfo(res.data.info[0]);
    }).catch(error => {
        console.log(error.response);
        let err = error.response.data.errors[0].msg;
        console.log(err);
        if (err){
        }
        else{
          setbackendError('');
          history.push("/feed");
          setIsSubmitted(true);
      }});
  }
  useEffect(()=> 
    getInfo()
  ,[]);
  return (
    <div>
      <NavBar/>
      <div style={{ backgroundImage: `url(${bg})`, display: 'flex',  justifyContent:'center', alignItems:'center', height: '100vh'}}>
        
        <Card border="danger" bg={"light".toLowerCase()}
          text={"light" === 'light' ? 'dark' : 'white'}
          style={{ width: '18rem' }}
          className="mb-2" style={{ width: '50rem', height: '44rem' }}>
          <Form className="form" onSubmit={handleSubmit}>
          <Card.Img variant="top" src="" />
          <Card.Body>
            <Card.Title className="makeCenter" style={{fontSize:28}}>Update Your Information</Card.Title>
            <br/>
            <Row>
              <Col>
                <FormGroup>
                  <Label className="createPostTitle makeCenter">Name</Label>
                  <Input
                    type="name"
                    name="name"
                    id="name"
                    value={values.name}
                    placeholder={values.old_name}
                    onChange= {handleChange}
                  />
                  {errors.name && <p className="text-danger">{errors.name}</p>}
                </FormGroup>
                <FormGroup>
                  <Label className="createPostTitle makeCenter">Email</Label>
                  <Input
                    type="email"
                    name="email"
                    id="email"
                    value={values.email}
                    placeholder={values.old_email}
                    onChange={handleChange}
                  />
                  {errors.email && <p className="text-danger">{errors.email}</p>}
                </FormGroup>
                <FormGroup>
                  <Label className="createPostTitle makeCenter">Address (District)</Label>
                  <Select options={Districts} value={Districts[values.search_location]}
                    onChange={handleChangeAddress} placeholder={values.old_address.toUpperCase()}></Select>
                  {errors.address && <p className="text-danger">{errors.address}</p>}
                </FormGroup>
                <FormGroup>
                  <Label className="createPostTitle makeCenter">Phone</Label>
                  <Input
                    type="phone"
                    name="phone"
                    id="phone"
                    value={values.phone}
                    placeholder={values.old_phone}
                    onChange={handleChange}
                  />
                  {errors.phone && <p className="text-danger">{errors.phone}</p>}
                </FormGroup>
              </Col>            
            <Row className="justify-content-evenly">
              <Col className='makeCenter'>
                <Button variant="danger" size="lg" onClick={()=> {history.push("/feed");}}>CANCEL</Button>
              </Col>
              <Col className='makeCenter'>
                <Button variant="success" size="lg" type="submit">UPDATE</Button>
              </Col>
              {backend_error && <h3 className="text-black text-center">{backend_error}</h3>}
            </Row></Row>
          <Card.Text>
          </Card.Text>
          <Col className='makeCenter'>
                <Button variant="primary" size="lg" onClick={()=> {history.push("/changePassword");}} >Change password?</Button>
              </Col>
              <div style={{marginTop:"5%"}}>
              <Col className='makeCenter'>   
              <Button className=' btn btn-warning' onClick={logout}>Log Out</Button></Col>
              </div>
          </Card.Body>
          </Form>
        </Card>
      </div>
    </div>
  );
}

export default Settings
