import './App.css';
import React, {useState} from 'react';
import Axios from 'axios';
import {
  Button,
  Form,
  FormGroup,
  Input
} from 'reactstrap';
import { config } from './config';
import useToken from './useToken';
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import PostCard from './postCard';
import bg from './assets/bg.jpg';
import NavBar from './components/NavBar/NavBar';
import Select from 'react-select'
import { useHistory, Redirect } from "react-router-dom";

export default function Search(){
  const history = useHistory();
  const tokenString = sessionStorage.getItem('token');
  const [values, setValues] = useState({
    posts: {},
    search_name: '',
    search_location: '',
    search_breed: '',
    message: ''
  });
  
  const Districts = [
    { value: "ADALAR", label: "ADALAR" },
    { value: "ARNAVUTKY", label: "ARNAVUTKY" },
    { value: "ATAEHR", label: "ATAEHR" },
    { value: "AVCILAR", label: "AVCILAR" },
    { value: "BACILAR", label: "BACILAR" },
    { value: "BAHELEVLER", label: "BAHELEVLER" },
    { value: "BAKIRKY", label: "BAKIRKY" },
    { value: "BAAKEHR", label: "BAAKEHR" },
    { value: "BAYRAMPAA", label: "BAYRAMPAA" },
    { value: "BEYKOZ", label: "BEYKOZ" },
    { value: "BEYLKDZ", label: "BEYLKDZ" },
    { value: "BEYOLU", label: "BEYOLU" },
    { value: "BYKEKMECE", label: "BYKEKMECE" },
    { value: "ATALCA", label: "ATALCA" },
    { value: "EKMEKY", label: "EKMEKY" },
    { value: "ESENLER", label: "ESENLER" },
    { value: "ESENYURT", label: "ESENYURT" },
    { value: "EYPSULTAN", label: "EYPSULTAN" },
    { value: "FATH", label: "FATH" },
    { value: "GAZOSMANPAA", label: "GAZOSMANPAA" },
    { value: "GNGREN", label: "GNGREN" },
    { value: "KADIKY", label: "KADIKY" },
    { value: "KAITHANE", label: "KAITHANE" },
    { value: "KARTAL", label: "KARTAL" },
    { value: "KKEKMECE", label: "KKEKMECE" },
    { value: "MALTEPE", label: "MALTEPE" },
    { value: "KKEKMECE", label: "KKEKMECE" },
    { value: "PENDK", label: "PENDK" },
    { value: "SANCAKTEPE", label: "SANCAKTEPE" },
    { value: "SARIYER", label: "SARIYER" },
    { value: "SLVR", label: "SLVR" },
    { value: "SULTANBEYL", label: "SULTANBEYL" },
    { value: "SULTANGAZ", label: "SULTANGAZ" },
    { value: "LE", label: "LE" },
    { value: "L", label: "L" },
    { value: "TUZLA", label: "TUZLA" },
    { value: "MRANYE", label: "MRANYE" },
    { value: "SKDAR", label: "SKDAR" },
    { value: "ZEYTNBURNU", label: "ZEYTNBURNU" }
  ]

  const breedOptions = [
    { value: 'cat', label: 'Cat' },
    { value: 'dog', label: 'Dog' },
    { value: 'bird', label: 'Bird' }
  ]

  const handleChangeAddress = e => {
    const p_value = e.value;
    setValues({
      ...values,
      ["search_location"] : p_value
    });
    values.search_location = e.value;
  };

  function Reset(){

    history.go(0);
  }
  const handleChangeBreed = e => {
    const p_value = e.value;
    setValues({
      ...values,
      ["search_breed"] : p_value
    });
    values.search_breed = e.value;
  };

  async function getData(){
    Axios.post(`${config.SERVER_URI}/api/search`,
    {
      search_breed: '%' + (values.search_breed).toLowerCase() + '%',
      search_name: '%' + (values.search_name).toUpperCase() + '%' ,
      search_location: '%' + (values.search_location).toLowerCase() + '%' 
    }).then( response => {
      console.log(response);
      handleChangePosts(response.data.posts);
    }).catch(error => {
      console.log(error.response);
      let err = error.response.data.errors[0].msg;
      console.log(err);
    });

    setValues({
      ...values,
      ["message"] : "Sorry, we could not find any post as you searched."
    });
    values.message = "Sorry, we could not find any post as you searched.";
  }
  function handleSubmit(e){
    const {name, value} = e.target;
    setValues({
      ...values,
      [name] : value
    });
    e.preventDefault();
    
    getData();    
  };
  const handleChangePosts = (e) => {
    setValues({
      ...values,
      ["posts"] : e
    });
    values.posts = e;
    };
  const handleChange = e => {
    const {name, value} = e.target;
    setValues({
      ...values,
      [name] : value
    });
  };
  return (
    <div style={{ 
      backgroundImage: `url(${bg})`,  backgroundPosition: 'center',
      height:"100%"}}>
      <NavBar/>
      
      <Form onSubmit={handleSubmit}>
        <Row className="makeCenter">
          <Col md={3} >
          <FormGroup>
            <label>Breed</label>
            <Select options={breedOptions} value={breedOptions[values.search_breed]}
              onChange={handleChangeBreed}></Select>
          </FormGroup>
          </Col>
          <Col md={3} className="my-1">
          <FormGroup>
            <label>Name</label>
          <Input
                name="search_name"
                id="search_name"
                value={values.search_name}
                onChange={handleChange}
              />
          </FormGroup>
          </Col>
          <Col md={3} className="my-1">
          <FormGroup>
            <label>Location</label>
          <Select options={Districts} value={Districts[values.search_location]}
                    onChange={handleChangeAddress}></Select>
          </FormGroup>
          </Col>
          <Col md={1} className="my-1">
          <Button className="makeCenter btn btn-success" variant="success" size="lg" type="submit">Search</Button>
          </Col>
          <Col md={1} className="my-1">
          <Button className="makeCenter btn btn-danger" variant="danger" size="lg" onClick={Reset}>Reset</Button>
          </Col>
        </Row>
      </Form>
      
      <div style={{ 
        backgroundImage: `url(${bg})`,  backgroundPosition: 'center',
      }} className="makeCenter">
      <Row>
        {
          values.posts.length > 0 ?
          values.posts.map((p, i) => (
            <Col xs={6} className="makeCenter">
              <PostCard
                user_id={tokenString==p.user_id}
                name={p.name}
                breed={p.breed}
                age={p.age}
                location={p.location}
                extra_info={p.extra_info}
                p_image={p.p_image}
                vaccinated={p.vaccinated}
                ts={p.ts}
              />
            </Col>
          )) : <h2>{values.message}</h2>
        }
        </Row>
      </div>
      </div>

  );
}