import './App.css';
import React, {useState} from 'react';
import { useHistory } from "react-router-dom";
import Axios from 'axios';
import {
  Button,
  Card,
  Form,
  FormGroup,
  Input,
  Label,
} from 'reactstrap';
import { config } from './config';
import Select from 'react-select'

function validateInfo(values){
  let errors ={}
  if (!values.name.trim()){
    errors.name = "Name required"
  }
  if (!values.email.trim()){
    errors.email = "Email required"
  }
  else if (!/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(values.email)){
    errors.email = "Email address is invalid"
  }

  if (!values.phone.trim()){
    errors.phone = "Phone required"
  }

  if (!values.password){
    errors.password = "Password is required"
  }
  else if (values.password.length < 6){
    errors.password = "Password needs to be 6 characters or more."
  }

  if (!values.password2){
    errors.password2 = "Password is required"
  }
  else if (values.password2 !== values.password){
    errors.password2 = "Passwords do not match"
  }
  return errors;
}


function Register() {
  const [values, setValues] = useState({
    name: '',
    email: '',
    phone: '',
    address: '',
    password: '',
    password2: ''
  });

  const Districts= [
    { value: "ADALAR", label: "ADALAR" },
    { value: "ARNAVUTKY", label: "ARNAVUTKY" },
    { value: "ATAEHR", label: "ATAEHR" },
    { value: "AVCILAR", label: "AVCILAR" },
    { value: "BACILAR", label: "BACILAR" },
    { value: "BAHELEVLER", label: "BAHELEVLER" },
    { value: "BAKIRKY", label: "BAKIRKY" },
    { value: "BAAKEHR", label: "BAAKEHR" },
    { value: "BAYRAMPAA", label: "BAYRAMPAA" },
    { value: "BEYKOZ", label: "BEYKOZ" },
    { value: "BEYLKDZ", label: "BEYLKDZ" },
    { value: "BEYOLU", label: "BEYOLU" },
    { value: "BYKEKMECE", label: "BYKEKMECE" },
    { value: "ATALCA", label: "ATALCA" },
    { value: "EKMEKY", label: "EKMEKY" },
    { value: "ESENLER", label: "ESENLER" },
    { value: "ESENYURT", label: "ESENYURT" },
    { value: "EYPSULTAN", label: "EYPSULTAN" },
    { value: "FATH", label: "FATH" },
    { value: "GAZOSMANPAA", label: "GAZOSMANPAA" },
    { value: "GNGREN", label: "GNGREN" },
    { value: "KADIKY", label: "KADIKY" },
    { value: "KAITHANE", label: "KAITHANE" },
    { value: "KARTAL", label: "KARTAL" },
    { value: "KKEKMECE", label: "KKEKMECE" },
    { value: "MALTEPE", label: "MALTEPE" },
    { value: "KKEKMECE", label: "KKEKMECE" },
    { value: "PENDK", label: "PENDK" },
    { value: "SANCAKTEPE", label: "SANCAKTEPE" },
    { value: "SARIYER", label: "SARIYER" },
    { value: "SLVR", label: "SLVR" },
    { value: "SULTANBEYL", label: "SULTANBEYL" },
    { value: "SULTANGAZ", label: "SULTANGAZ" },
    { value: "LE", label: "LE" },
    { value: "L", label: "L" },
    { value: "TUZLA", label: "TUZLA" },
    { value: "MRANYE", label: "MRANYE" },
    { value: "SKDAR", label: "SKDAR" },
    { value: "ZEYTNBURNU", label: "ZEYTNBURNU" }
  ]

  const handleChangeAddress = e => {
      const p_value = e.value;
      setValues({
        ...values,
        ["address"] : p_value
      });
      values.address = e.value;
    };

  const [errors, setErrors] = useState('');
  const [backend_error, setbackendError] = useState('');

  const [isSubmitted, setIsSubmitted] = useState(false);
  const history = useHistory();

  const handleChange = e => {
    const {name, value} = e.target;
    setValues({
      ...values,
      [name] : value
    });
  };

  const handleSubmit = e => {
    console.log("handleSubmit");
    e.preventDefault();
    setErrors(validateInfo(values))
    if (errors==={}){
      console.log("no error");

    }
    register();
  }


  const register = () => {
    console.log("in");
    Axios.post(`${config.SERVER_URI}/api/register`,
    {
      name:values.name,
      email:values.email,
      phone:values.phone,
      address:values.address.toLowerCase(),
      password:values.password
    }).then(response => {
      if (!response){
        console.log("no error");
      }
      setIsSubmitted(true);
      history.push("/login");
    }).catch(error => {
        console.log(error.response);
        let err = error.response.data.errors[0].msg;
        console.log(err);
        if (err){
          console.log(err);
          setbackendError(err);
          setValues({
            name: '',
            email: '', 
            phone: '',
            address: '',
            password:'',
            password2: ''
          });
          err ='';
        }
        else{
          setbackendError('');
          history.push("/login");
          setIsSubmitted(true);
      }});
  };

  return (
    <div className="App">
      <Card style={{height:"100vh", overflow: "auto", backgroundColor:"rgb(44, 34, 7)"}}>
      <div className="register-form"> 
        <Form className="form" onSubmit={handleSubmit}>
        {isSubmitted ? <span>Success! Thank you for registering.</span>: null}
        <h2 className="text-warning text-center">Register</h2>
          <FormGroup>
            <Label className="text-white">Name-Surname</Label>
            <Input
              type="name"
              name="name"
              id="name"
              value={values.name}
              onChange={handleChange}
            />
            {errors.name && <p className="text-danger">{errors.name}</p>}
          </FormGroup>
          <FormGroup>
            <Label className="text-white">Email</Label>
            <Input
              type="email"
              name="email"
              id="email"
              value={values.email}
              onChange={handleChange}
            />
            {errors.email && <p className="text-danger">{errors.email}</p>}
          </FormGroup>
          <FormGroup>
            <Label className="text-white">Phone Number</Label>
            <Input
              type="number"
              name="phone"
              id="phone"
              value={values.phone}
              onChange={handleChange}
            />
            {errors.phone && <p className="text-danger">{errors.phone}</p>}
          </FormGroup>
          <FormGroup>
            <Label className="text-white">Address (District)</Label>
            <Select options={Districts} value={Districts[values.address]}
                  onChange={handleChangeAddress}></Select>
          </FormGroup>
          <FormGroup>
            <Label className="text-white">Password</Label>
            <Input
              type="password"
              name="password"
              id="password"
              value={values.password}
              onChange={handleChange}
            />
            {errors.password && <p className="text-danger">{errors.password}</p>}
          </FormGroup>
          <FormGroup>
            <Label className="text-white">Password</Label>
            <Input
              type="password"
              id="password2"
              name="password2"
              value={values.password2}
              onChange={handleChange}
            />
            {errors.password2 && <p className="text-danger">{errors.password2}</p>}\
          </FormGroup>

        <div className="row justify-content-evenly">
          <div className="col-6">
            <Button className="btn-outline">Register</Button>
          </div>
        
          <div className="col-3 makeCenter">
            <label className="text-danger">Already have an account?</label>
          </div>
          <div className="col-3 makeCenter">
            <Button className="btn-outline ml-1" onClick={()=> {history.push("/login");}}>Login</Button>
          </div>
        {backend_error && <h3 className="text-white text-center">{backend_error}</h3>}
        </div> 
        </Form>
        </div>
        </Card>
        </div>
  );
}

export default Register;
